
a {
    color: darkslategray;
}
a:link {
    text-decoration: none;
}

.nav {
    padding: 20px 20px 40px 20px;
    display: flex;
    background-color: rgb(29,174,235);
    align-items: center;
    flex-wrap: wrap;
}

.nav-logo-image {
    max-width: 100px;
    width: 25%; /* any larger would wrap on iPhone SE */
}
.nav-logo-text {
    color: white;
    padding-left: 20px;
}
.logo-title {
    font-size: 55px;
    white-space: pre; /* no wrap */
}
.logo-subtitle {
    font-size: 14px;
}

.nav-items {
    flex-grow: 1; /* grow this instead of logo */
    flex-shrink: 1; /* shink menu items */
    flex-basis: 100px; /* shrink when this min size is hit */
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-content: flex-start;
    padding-left: 10px;
}
.nav-items * {
    color: white;
    padding-right: 10px;
    list-style-type: none;
    font-size: 17px;
}