
.home-content {
    display: flex;
    flex-wrap: wrap-reverse; /* wrap screenshot to below benefits */
    align-items: flex-end; /* ensure screenshot does not resize vertically */
}
.content-benefits {
    margin: 10px 20px 50px 20px; /* so screenshot can have neg top margin */
    flex-basis: 500px; /* must hit this before it will wrap */
    flex-shrink: 1; /* shrink this first */
    flex-grow: 100; /* grow this part, mostly, not the screenshot */
}
.content-benefit {
    border-radius: 20px;
    padding: 20px 20px 0px 20px;
    background-color: #ece7e7;
    display: flex;
    align-items: flex-start;
    margin-top: 10px;
}
.benefits-text-term {
    font-weight: bold;
    padding-left: 20px;
}

.content-app {
    flex-basis: 320px; /* limit to smallerst iPhone width to avoid horizontal scrolling */
    flex-grow: 1; /* must have in order to fill row and center contents when on separate row */
    display: flex;
    justify-content: center; /* center when screenshot on second row */
    flex-wrap: wrap;
    align-items: flex-start; /* do not vertically stretch items */
}
.break {
    width: 100%; /* force app store link image on separate row */
}
.screenshot {
    margin-top: -50px; /* stick into nav bar */
    max-width: 300px; /* limit size */
    width: 90%; /* fill most of width when on it's own row */
}
.app-store-link-img {
    max-width: 200px;
    padding-top: 20px;
}